import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import TextField from '@mui/material/TextField';

const Verify = ({otp, setOtp, handleVerifyOtp}) => {
    return (
        <div className="card">
        <h1 className="sub-heading">Enter OTP</h1>
        <div className="login form">
           <FormControl fullWidth style={{margin: '15px 0'}}>
              <TextField defaultValue={otp} onChange={(e) => setOtp(e.target.value)} type='text' label="OTP" variant="outlined" />
           </FormControl>
           <button onClick={handleVerifyOtp} className='btn btn-full btn-gradient'>Verify</button>
        </div>
    </div>
    )
}

export default Verify;