
import './App.css';
import Layout from './Layout';
import { createBrowserRouter, RouterProvider, Navigate } from 'react-router-dom';





import Dashboard from './pages/Dashboard';



import Posts from './pages/posts/Posts';
import AddPost from './pages/posts/AddPost';
import EditPost from './pages/posts/EditPost';

import Login from './pages/Login';


import { AuthContext, AuthDispatchContext } from './context/AuthContext';
import { useEffect, useReducer } from 'react';
import { authRedicer } from './reducers/authReducer';

import Splash from './pages/Splash';

import { ToastContainer, toast } from 'react-toastify';

import 'react-toastify/dist/ReactToastify.css';

import { autoLogin } from './http';


import BookingRequests from './pages/booking-request/BookingRequests';
import ViewBooking from './pages/booking-request/ViewBooking';

import ForgotPassword from './pages/password-update/ForgotPassword';
import Contact from './pages/contact/Contact';



const router = createBrowserRouter([
  {
    path: '/admin',
    element: <Layout />,
    children: [
        {
          path: '/admin',
          element: <Dashboard />
        },
        {
          path: '/admin/posts',
          element: <Posts />
        },
        {
          path: '/admin/posts/add',
          element: <AddPost />
        },
        {
          path: '/admin/posts/edit/:id',
          element: <EditPost />
        },
        
        {
          path: '/admin/application/view/:id',
          element: <ViewBooking />
        },
        {
          path: '/admin/applications',
          element: <BookingRequests />
        },
        {
          path: '/admin/contact',
          element: <Contact />
        }
    ]
  }
])


const unAuthroute = createBrowserRouter([
  {
    path: '/admin',
    element: <Login />
  },
  {
    path: '/admin/forgot-password',
    element: <ForgotPassword />
  },
    {
    path: '/admin/*', // catch-all for anything under /admin
    element: <Navigate to="/admin" replace />
  }
])

function App() {

  const [auth, dispatch] = useReducer(authRedicer, {loading: true})

  useEffect(() => {
    const chechAuth = async () => {
      try {
        const {data} = await autoLogin();
        if(data.auth == false){
          dispatch({type: 'notauthenticated'})
        }else{
          dispatch({type: 'login', admin: data.admin});
        }
      } catch (error) {
        console.log(error)
        dispatch({type: 'notauthenticated'})
      }
    }
    chechAuth();
  }, [])

  

  return (
    <div className="App">
       <AuthContext.Provider value={auth}>
        <AuthDispatchContext.Provider value={dispatch}>
          {auth.loading ? <Splash /> : <RouterProvider router={auth._id ? router : unAuthroute} />}
          <ToastContainer />
        </AuthDispatchContext.Provider>
       </AuthContext.Provider>
    </div>
  );
}

export default App;
