 

import axios from 'axios';

//https://bingenfiesta.com

const api = axios.create({
    baseURL: 'https://neonmortgage.com/api/admin',
    withCredentials: true,
    headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
    }
})

export const loginAdmin = async (data) => api.post('/auth/login', data);
export const autoLogin = async () => api.get(`/auth/auto`);
export const logountAdmin = async () => api.get('/auth/logout');
export const sendOtp = async (data) => api.post('/auth/otp', data);
export const verifyOtp = async (data) => api.post('/auth/verify', data);
export const updatePassword = async (data) => api.post('/auth/update', data)







//blogs
export const getPosts = async (page, search) => api.get(`/blog/get/${page}?s=${search}`);
export const createBlog = async (data) => api.post('/blog/create', data);
export const getPost = async (id) => api.get(`/blog/getbyid/${id}`);
export const deletePost = async (id) => api.get(`/blog/delete/${id}`);
export const editPost = async (id, data) => api.post(`/blog/update/${id}`, data);

export const getCallbacks = async (page) => api.get(`/callback/get/${page}`);
export const deleteCallback = async (id) => api.get(`/callback/delete/${id}`)





//users
export const getUsers = async (page = 1) => api.get(`/user/get/${page}`);
export const deleteUserReq = async (id) => api.get(`/user/delete/${id}`)

//contact
export const getContact = async (page = 1) => api.get(`/contact/get/${page}`);
export const deleteContact = async (id) => api.get(`/contact/delete/${id}`)

//booking
export const getBooking = async (page = 1, s) => api.get(`/bookings/get/${page}?s=${s}`);
export const getBookingById = async (id) => api.get(`/booking/getone/${id}`);
export const deleteBooking = async (id) => api.get(`/booking/delete/${id}`);
export const exportApplication = async () => api.get('/application/export', {responseType: 'blob'});



export const getDashboard = async (s = 0) => api.get(`/dashboard/get?s=${s}`);

export const getBookingRequests = async (page = 1) => api.get(`/bookings/get/${page}`);

export const deleteBookingRequest = async (id) => api.get(`/bookingrequest/delete/${id}`);

export const getApplication = async (page = 1) => api.get(`/application/get/${page}`)

export const getApplicationByID = async (id) => api.get(`/application/getbyid/${id}`);
export const updateApplication = async (id, data) => api.post(`/application/update/${id}`, data);
export const deleteApplication = async (id) => api.get(`/application/delete/${id}`);

//eibor

export const updateEibor = async (data) => api.post('/eibor/update', data);
export const getEibor = async () => api.get('/eibor/get')