import Otp from "./Otp";
import Verify from "./Verify";
import Update from "./Update";

import { useState } from "react";

import { sendOtp, verifyOtp, updatePassword } from "../../http";
import {useNavigate} from 'react-router-dom';
import { toast } from 'react-toastify';



const ForgotPassword = () => {

    const [step, setStep] = useState(1);
    const [email, setEmail] = useState('');
    const [otp, setOtp] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPaddword, setConfirmPasswrod] = useState('');
    const [adminId, setAdminId] = useState('');
    const [hash, setHash] = useState('');

    const navigate = useNavigate();

    const handleSendOtp = async () => {
        try {
            const {data} = await toast.promise(
                sendOtp({email}),
                {
                  pending: 'Sending OTP...',
                  success: 'OTP sent',
                }
            )
            setHash(data.hash);
            console.log(data.otp)
            setStep(2)
        } catch (error) {
            toast.error(error.response.data.message, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            })
            return
        }
    }

    const handleVerifyOtp = async () => {
        try {
            const {data} = await toast.promise(
                verifyOtp({email, otp, hash}),
                {
                  pending: 'Verifying OTP...',
                  success: 'OTP Verified',
                }
            )
            setAdminId(data.admin_id);
            setStep(3)
        } catch (error) {
            toast.error(error.response.data.message, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            })
            return
        }
    }

    const handleUpdate = async () => {
        try {
            const {data} = await toast.promise(
                updatePassword({password, confirmPaddword, adminId}),
                {
                  pending: 'Updating password',
                  success: 'Password updated',
                }
            )
            navigate('/admin')
        } catch (error) {
            toast.error(error.response.data.message, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            })
            return
        }
    }

    return (
        <div className="loginpage">
            <div className="row">
                <div className="col login-left"></div>
                <div className="col login-right">
                    <div className="login-content">
                        <img src="/admin/logoc.png" />
                        {step == 1 && <Otp email={email} setEmail={setEmail } handleSendOtp={handleSendOtp} />}
                        {step == 2 && <Verify otp={otp} setOtp={setOtp} handleVerifyOtp={handleVerifyOtp} />}
                        {step == 3 && <Update password={password} setPassword={setPassword} confirmPaddword={confirmPaddword} setConfirmPasswrod={setConfirmPasswrod} handleUpdate={handleUpdate} />}
                    </div>
                    <span style={{position: 'absolute', bottom: '30px'}}>
                    <span className="footer-link">Powered By <a href="https://www.waytowebs.com" target="_blank" className="footer-link"><img style={{width:'20px', margin: '0 2px'}} src="/admin/assets/img/waytowebs-icon.png" />Waytowebs</a></span>
                </span>
                </div>
            </div>
        </div>
    )
}

export default ForgotPassword;