import { NavLink } from "react-router-dom";
import { useState, useEffect } from "react";
import { getPosts, deletePost } from "../../http";
import {toast} from 'react-toastify';
import Pagination from '@mui/material/Pagination';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';


const Posts = () => {

    const [posts, setPosts] = useState([]);
    const [reload, setReload] = useState(false)
    const [page, setPage] = useState(1);
    const [total, setTotal] = useState(0);
    const [search, setSearch] = useState('')

    useEffect(() => {
        const fetchDecoration = async () => {
            try {

                const {data} = await getPosts(page, search);
                console.log('posts',data)
                setPosts(data.posts)
                setTotal(data.total)
                
            } catch (error) {
                toast.error(error.response.data.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                })
            }
        }

        fetchDecoration();
    }, [reload, page, search])

    const handleDelete = async (id) => {
        try {
            const {data} = await toast.promise(
                deletePost(id),
                {
                  pending: 'Deleting Post...',
                  success: 'Post Deleted',
                }
            )
           setReload(!reload)
           
        } catch (error) {
            toast.error(error.response.data.message, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            })
            return
        }
    }

    const updateSearch = (term) => {
        setPage(1);
        setSearch(term)
    }


    return (
        <div className="inner-pages">
        <div className="page-header">
            <div className="page-leader-col">
                <h1 className="heading-primary">All Posts</h1>
            </div>
            <div className="page-leader-col" style={{display: 'flex', width: 'auto', alignItems: 'center'}}>
                <FormControl fullWidth style={{margin: '15px 10px'}}>
                    <TextField value={search} onChange={(e) => updateSearch(e.target.value)} label="Search" variant="outlined" />
                </FormControl>
                <NavLink to={'/admin/posts/add'}><button type="button" class="btn btn-gradient" style={{whiteSpace: 'nowrap'}}>Add Post</button></NavLink>
            </div>
        </div>
        <div className="page-content grid">
            

            {
                posts.map((post) => (
                    <div className="page-content-grid-item addon-item">
                        <div className="card">
                            <div className="card-thumb">
                                <img src={post.image} />
                            </div>
                            <div className="cand-content" style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                <h2 className="accent-heading">{post.title}</h2>
                            </div>
                            <div className="card-action">
                                <NavLink to={`/admin/posts/edit/${post._id}`}><button class='action-btn edit'>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pencil" viewBox="0 0 16 16">
                                        <path d="M12.146.146a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1 0 .708l-10 10a.5.5 0 0 1-.168.11l-5 2a.5.5 0 0 1-.65-.65l2-5a.5.5 0 0 1 .11-.168zM11.207 2.5 13.5 4.793 14.793 3.5 12.5 1.207zm1.586 3L10.5 3.207 4 9.707V10h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.293zm-9.761 5.175-.106.106-1.528 3.821 3.821-1.528.106-.106A.5.5 0 0 1 5 12.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.468-.325" />
                                    </svg>
                                    <span>Edit</span>
                                </button></NavLink>
                                <button onClick={() => handleDelete(post._id)} class='action-btn delete'>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
                                        <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5m2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5m3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0z" />
                                        <path d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4zM2.5 3h11V2h-11z" />
                                    </svg>
                                    <span>Delete</span>
                                </button>
                            </div>
                        </div>
                    </div>
                ))
            }

            
        </div>

        <div className='pgination-box'>
            <Pagination count={Math.ceil(total / 6)} onChange={(e, v) => setPage(v)} page={page} variant="outlined" color="secondary" />
        </div>
    </div>
    )
}
export default Posts;