import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import TextField from '@mui/material/TextField';
import { useState, useEffect } from 'react';
import { updateEibor, getEibor } from '../http';
import { toast } from 'react-toastify';


const Dashboard = () => {

    const [dashboard, setDashboard] = useState({});
    const [s, setS] = useState(false);
    const [seting, setSeting] = useState({});

    const [three_month, setThreeMont] = useState();
    const [six_month, setSixMonth] = useState();

    useEffect(() => {
            const fetcEibor = async () => {
                try {
                    const {data} = await getEibor();
                    setThreeMont(data.three_month);
                    setSixMonth(data.six_month)
                } catch (error) {
                    toast.error(error.response.data.message, {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                        theme: "light",
                    })
                }
            }
            fetcEibor();
        }, [])

    const handleUpdate = async () => {
            try {
                const {data} = await toast.promise(
                    updateEibor({three_month, six_month}),
                    {
                      pending: 'Updaing EIBOR...',
                      success: 'EIBOR Updated',
                    }
                )
                setThreeMont(data.three_month);
                setSixMonth(data.six_month)
            } catch (error) {
                toast.error(error.response.data.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                })
                return
            }
        }



  


    return (
        <div className="inner-pages">
        <div className="page-header">
            <div className="page-leader-col">
                <h1 className="heading-primary">Dashboard</h1>
            </div>
            
        </div>
        
        <div style={{marginTop: '30px'}}>
            <h3 className='accent-heading'>Current Date EIBOR</h3>
            <div style={{display: 'flex', gap: '20px', marginTop:'20px'}}>
                <div>
                    <p className='text'>3 Month EIBOR(%)</p>
                    <TextField value={three_month} onChange={(e) => setThreeMont(e.target.value)} type='number' />
                </div>
                <div>
                    <p className='text'>6 Month EIBOR(%)</p>
                    <TextField value={six_month} onChange={(e) => setSixMonth(e.target.value)} type='number' />
                </div>
            </div>
            <button onClick={handleUpdate} type="button" class="btn btn-gradient" fdprocessedid="exjdqk" style={{marginTop: '20px'}}>Update EIBOR</button>
        </div>
        </div>
            
    )
}
export default Dashboard;