import Pagination from '@mui/material/Pagination';


import TextField from '@mui/material/TextField';

import { useState, useEffect } from 'react';
import { getApplication, deleteApplication, exportApplication } from '../../http';
import { toast } from 'react-toastify';
import dayjs from 'dayjs';
import { Tooltip } from '@mui/material';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { NavLink } from 'react-router-dom';

const BookingRequests = () => {

    const [requests, setRequests] = useState([]);
    const [page, setPage] = useState(1);
    const [s, setS] = useState('');
    const [reload, setReload] = useState(false);
    const [total, setTotal] = useState();
    const [order, setOrder] = useState(1);
    const [ststus, setStatus] = useState('all')

    useEffect(() => {
        const fetchRequests = async () => {
            try {

                const {data} = await getApplication(page);
                console.log('booking', data)
                setRequests(data.applications)
                setTotal(data.total)
                
            } catch (error) {
                toast.error(error.response.data.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                })
            }
        }

        fetchRequests();
    }, [reload, page])

    const handleDelete = async (id) => {
        try {
            const {data} = await toast.promise(
                deleteApplication(id),
                {
                  pending: 'Deleting Application...',
                  success: 'Application Deleted',
                }
            )
           setReload(!reload)
           
        } catch (error) {
            toast.error(error.response.data.message, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            })
            return
        }
    }

    const handleExportBookings = async () => {
        try {
          const response = await toast.promise(
            exportApplication(),
            {
              pending: 'Exporting application...',
              success: 'Download started!',
            }
          );
      
          // Create a download link
          const blob = new Blob([response.data], { type: 'text/csv' });
          const url = window.URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url;

          const now = new Date();
          const day = String(now.getDate()).padStart(2, '0');
          const month = String(now.getMonth() + 1).padStart(2, '0'); // Months are 0-indexed
          const year = now.getFullYear();

          const fileName = `applications-${day}-${month}-${year}.csv`;
          a.download = fileName;
          document.body.appendChild(a);
          a.click();
          a.remove();
        } catch (error) {
          toast.error(
            error.response?.data?.message || 'Failed to export bookings',
            {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              theme: "light",
            }
          );
        }
      };

    return (
        <div className="inner-pages">
        <div className="page-header">
            <div className="page-leader-col">
                <h1 className="heading-primary">Applications</h1>
            </div>
            <div className="page-leader-col">
            <button onClick={handleExportBookings} type="button" class="btn btn-gradient" fdprocessedid="exjdqk">Export</button>
            </div>
        </div>

        <div style={{marginTop: '30px'}}>
            <div className="card">
                <div className="table-box">
                    <table cellSpacing={0}>
                        <thead>
                            <tr>
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Name</strong></span></td>
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Email</strong></span></td>
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Mobile</strong></span></td>
                                {/* <td><span className="text" style={{fontSize: '14px'}}><strong>Location</strong></span></td> */}
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Status</strong></span></td>
                               
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Date</strong></span></td>
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Action</strong></span></td>
                            </tr>
                        </thead>
                        <tbody>
                                {
                                    requests.map((request) => (
                                        <tr>
                                    <td><span className="text">{request.name}</span></td>
                                    <td>
                                    <span className="text">{request.email}</span>
                                    </td>
                                    <td><span className="text">{request.phone}</span></td>
                                    <td>
                                          <span className="text">{request.status}</span>
                                     </td>
                                    
                                    <td>
                                    
                                        <span className="text">{dayjs(request.createdAt).format('D MMMM, YYYY')}</span>
                                      
                                    </td>
                                    <td>
                                        <NavLink to={`/admin/application/view/${request._id}`}><button class='action-btn edit'>
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-eye" viewBox="0 0 16 16">
  <path d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8M1.173 8a13 13 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5s3.879 1.168 5.168 2.457A13 13 0 0 1 14.828 8q-.086.13-.195.288c-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5s-3.879-1.168-5.168-2.457A13 13 0 0 1 1.172 8z"/>
  <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5M4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0"/>
</svg>
                                            <span>View</span>
                                        </button></NavLink>
                                        <button onClick={() => handleDelete(request._id)} class='action-btn delete'>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
                                                <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5m2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5m3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0z" />
                                                <path d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4zM2.5 3h11V2h-11z" />
                                            </svg>
                                            <span>Delete</span>
                                        </button>
                                    </td>
                                </tr>
                                    ))
                                }
                           
                            
                        </tbody>
                    </table>

                    <div className='pgination-box'>
                      <Pagination count={Math.ceil(total / 10)} onChange={(e, v) => setPage(v)} page={page} variant="outlined" color="secondary" />
                    </div>
                </div>
            </div>
        </div>
    </div>
    )
}

export default BookingRequests;