

export const authRedicer = (auth, action) => {
    switch (action.type) {
        case 'login': {
            return {...action.admin, loading: false}
        }
        case 'logout': {
           return {loading: false}
        }
        case 'notauthenticated' : {
            return {loading: false}
        }
        default:
        return auth;
    }
}