
import Pagination from '@mui/material/Pagination';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import TextField from '@mui/material/TextField';

import { useState, useEffect } from "react";

import { toast } from 'react-toastify';

import { getContact, deleteContact } from '../../http';

const Contact = () => {

    
    const [contacts, setContacts] = useState([]);
    const [page, setPage] = useState(1);
    const [reload, setReload] = useState(false);
    const [total, setTotal] = useState()

    useEffect(() => {
        const fetchContacts = async () => {
            try {

                const {data} = await getContact(page);
                console.log(data)
                setContacts(data.messages)
                setTotal(data.total)
                
            } catch (error) {
                toast.error(error.response.data.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                })
            }
        }

        fetchContacts();
    }, [reload])

    const handleDelete = async (id) => {
        try {
            const {data} = await toast.promise(
                deleteContact(id),
                {
                  pending: 'Deleting Contact...',
                  success: 'Contact Deleted',
                }
            )
           setReload(!reload)
           
        } catch (error) {
            toast.error(error.response.data.message, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            })
            return
        }
    }


    return (
        <div className="inner-pages">
        <div className="page-header">
            <div className="page-leader-col">
                <h1 className="heading-primary">Contact Form</h1>
            </div>
            <div className="page-leader-col">
               
            </div>
        </div>

        <div style={{marginTop: '30px'}}>
            <div className="card">
                <div className="table-box">
                    <table cellSpacing={0}>
                        <thead>
                            <tr>
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Name</strong></span></td>
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Email</strong></span></td>
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Mobile</strong></span></td>
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Message</strong></span></td>
                                <td><span className="text" style={{fontSize: '14px'}}><strong>Action</strong></span></td>
                            </tr>
                        </thead>
                        <tbody>
                                {
                                    contacts.map((con) => (
                                        <tr>
                                    <td><span className="text">{con.name}</span></td>
                                    <td><span className="text">{con.email}</span></td>
                                    <td><span className="text">{con.mobile}</span></td>
                                    <td><span className="text">{con.message}</span></td>
                                    <td>
                                        <button onClick={() => handleDelete(con._id)} class='action-btn delete'>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
                                                <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5m2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5m3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0z" />
                                                <path d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4zM2.5 3h11V2h-11z" />
                                            </svg>
                                            <span>Delete</span>
                                        </button>
                                    </td>
                                </tr>
                                    ))
                                }
                           
                            
                        </tbody>
                    </table>

                    <div className='pgination-box'>
                      <Pagination count={Math.ceil(total / 10)} onChange={(e, v) => setPage(v)} page={page} variant="outlined" color="secondary" />
                    </div>
                </div>
            </div>
        </div>
    </div>
    )
}

export default Contact;