import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { getApplicationByID } from "../../http";
import ImageGallery from "react-image-gallery";
import dayjs from "dayjs";
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import { updateApplication } from "../../http";
import { toast } from 'react-toastify';

const ViewBooking = () => {

    const {id} = useParams();
    const [application, setApplication] = useState({});

         const formatNumber = (num) => {
    return num.toLocaleString(undefined, {
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    });
  };

    const [status, setStatus] = useState([
        "Qualified lead",
        "Eligibility Check",
        "Follow up",
        "Requested a Call Back",
        "Called No Answer",
        "In process waiting for proposal sent",
        "In process waiting for documents",
        "Handover payment follow up",
        "QA Lead Re assign",
        "Contacted for Property Purchase",
        "Disbursed",
        "Spam",
        "Real estate agent",
        "Closed not buying right now",
        "No Response Two",
        "No Response One",
        "Not Interested",
        "Converted To Application",
        "Lead Assigned",
        "Did Not Enquire",
        "Spam Personal Car Loan",
        "Closed Went With Bank",
        "Contact Made",
        "Closed Not Eligible",
        "Number not Available",
        "Incorrect Number",
        "Incorrect Number Email Sent"
      ]);
      

    useEffect(() => {
        const fetchBooking = async () => {
            try {
                const {data} = await getApplicationByID(id);
                setApplication(data);
            } catch (error) {
               console.log(error) 
            }
        }
        fetchBooking()
    }, [id])

    const handleupdate = async (id, status) => {
            try {
                const {data} = await toast.promise(
                    updateApplication(id, {status}),
                    {
                      pending: 'Updating status...',
                      success: 'Status Updated',
                    }
                )
               
            } catch (error) {
                toast.error(error.response.data.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                })
                return
            }
        }


    return (
        <div className="inner-pages">
           <div style={{maxWidth: '800px', width: '100%', margin: '0 auto'}}>
           <div className="page-leader-col">
                <h1 className="heading-primary" style={{textAlign: "center"}}>Application Details</h1>
            </div>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3', borderTop: '1px solid #e3e3e3', marginTop: '40px'}}>
                <p className="text"><strong>Name : </strong></p>
                <p className="text">{application.name}</p>
            </div>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Email : </strong></p>
                <p className="text">{application.email}</p>
            </div>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Mobile : </strong></p>
                <p className="text">{application.phone}</p>
            </div>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>What are you looking for?</strong></p>
                <p className="text">{application.looking_for}</p>
            </div>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>What is your residency status?</strong></p>
                <p className="text">{application.residency_status}</p>
            </div>
            {application.looking_for === 'Mortgage Refinance' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Which bank is your mortgage with?</strong></p>
                <p className="text">{application.mortgage_bank}</p>
            </div>) : null}
            {application.looking_for === 'Mortgage Refinance' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>What type of property are you interested in?</strong></p>
                <p className="text">{application.property_purchasing_type}</p>
            </div>) : null}
            {application.looking_for === 'New Purchase' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Property Value</strong></p>
                <p className="text">{formatNumber(parseInt(application.property_value))}</p>
            </div>) : null}
            {application.looking_for === 'Mortgage Refinance' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Outstanding Loan Amount</strong></p>
                <p className="text">{formatNumber(parseInt(application.outstanding_loan_amount))}</p>
            </div>) : null}
            {application.looking_for === 'Mortgage Refinance' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Monthly Installment Amount</strong></p>
                <p className="text">{formatNumber(parseInt(application.monthly_installment_amount))}</p>
            </div>) : null}
            {application.looking_for === 'Mortgage Refinance' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Remaining Tenure</strong></p>
                <p className="text">{application.remaining_tenure}</p>
            </div>) : null}
            {application.looking_for === 'New Purchase' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Have you found a property already?</strong></p>
                <p className="text">{application.property_finding_status}</p>
            </div>) : null}
            {application.looking_for === 'New Purchase' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Are you looking for a ready-to-move property?</strong></p>
                <p className="text">{application.looking_for_move}</p>
            </div>) : null}
            {application.looking_for === 'New Purchase' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Mention the maximum amount you are willing to spend on the property</strong></p>
                <p className="text">{formatNumber(parseInt(application.max_amount_willing_to_spend))}</p>
            </div>) : null}
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>What’s your monthly income?</strong></p>
                <p className="text">{formatNumber(parseInt(application.monthly_income))}</p>
            </div>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Do you hold a UAE passport?</strong></p>
                <p className="text">{application.hold_uaepassport}</p>
            </div>
            {application.hold_uaepassport === 'No' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Are you a resident of the UAE?</strong></p>
                <p className="text">{application.resident_of_uae}</p>
            </div>) : null}
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>DOB</strong></p>
                <p className="text">{dayjs(application.dob).format('D MMMM, YYYY')}</p>
            </div>
            {application.hold_uaepassport === 'No' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>What passport do you hold?</strong></p>
                <p className="text">{application.passport_country}</p>
            </div>) : null}
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>What’s your employment status?</strong></p>
                <p className="text">{application.employement_status}</p>
            </div>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Would you be applying for a single or a joint application ?</strong></p>
                <p className="text">{application.application_type}</p>
            </div>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Do you have any credit cards?</strong></p>
                <p className="text">{application.have_credit_card}</p>
            </div>
            {application.have_credit_card === 'Yes' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>what is the total credit limit of all the cards combined?</strong></p>
                <p className="text">{formatNumber(parseInt(application.credit_limit))}</p>
            </div>) : null}
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Any other loans?</strong></p>
                <p className="text">{application.other_loan}</p>
            </div>
            {application.other_loan === 'Yes' ? (<div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>what is the total monthly repayment of all the loans combined?</strong></p>
                <p className="text">{formatNumber(parseInt(application.monthly_repayment))}</p>
            </div>) : null}

            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #e3e3e3'}}>
                <p className="text"><strong>Status</strong></p>
                <select onChange={(e) => handleupdate(application._id, e.target.value)}>
                    <option selected={application.status === 'Pending' ? true : false} value="Pending">Pending</option>
                    {
                        status.map((st) => <option selected={application.status === st ? true : false} value={st}>{st}</option>)
                    }
                </select>
            </div>
            
           
           </div>
        </div>
    )
}

export default ViewBooking;