import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import TextField from '@mui/material/TextField';

const Otp = ({email, setEmail, handleSendOtp}) => {
    return (
        <div className="card">
        <h1 className="sub-heading">Enter email address</h1>
        <div className="login form">
           <FormControl fullWidth style={{margin: '15px 0'}}>
              <TextField defaultValue={email} onChange={(e) => setEmail(e.target.value)} type='email' label="Email" variant="outlined" />
           </FormControl>
           <button onClick={handleSendOtp} className='btn btn-full btn-gradient'>Get OTP</button>
        </div>
    </div>
    )
}

export default Otp;