import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import TextField from '@mui/material/TextField';
import { getPost, editPost } from '../../http';
import { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import useEditImage from '../../hooks/useEditImage';
import { toast } from 'react-toastify';

import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';

const EditPost = () => {

    const [post, setPost] = useState({});
    const {image, handleFile, editImage} = useEditImage();
    const [title, setTitle] = useState('')
    const [short_des, setShortDes] = useState('');
    const [content, setContent] = useState('')
    const {id} = useParams();
    const navigate = useNavigate();

    const [slug, setSlug] = useState('');
    const [meta_title, setMetaTitle] = useState('');
    const [meta_description, setMetaDescription] = useState('');
    const [altText, setAltText] = useState('');


    useEffect(() => {
        const fetchDecoration = async () => {
            try {
                const {data} = await getPost(id);
                setPost(data.post)
                setTitle(data.post.title);
                setContent(data.post.content);
                setShortDes(data.post.short_des)
                setSlug(data.post.slug);
                setMetaTitle(data.post.meta_title);
                setMetaDescription(data.post.meta_description)
                setAltText(data.post.alt_text)
                
                console.log(data)
            } catch (error) {
                toast.error(error.response.data.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                })
            }
        }
        fetchDecoration();
    }, [id])

    const handleUpdate = async () => {
        try {
            const {data} = await toast.promise(
                editPost(id, {title, editImage: image, short_des, content, slug, alt_text: altText}),
                {
                  pending: 'Updating Post...',
                  success: 'Post Updated',
                }
            )
            navigate('/admin/posts')
           
        } catch (error) {
            toast.error(error.response.data.message, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
            })
            return
        }
    }

    const modules = {
        toolbar: [
            [{ 'header': '1' }, { 'header': '2' }, { 'header': '3' }, { 'header': '4' }, { 'header': '5' }, { 'font': [] }],
            [{ 'list': 'ordered'}, { 'list': 'bullet' }],
            ['bold', 'italic', 'underline', 'strike', 'blockquote'],
            [{ 'align': [] }],
            [{ 'color': [] }, { 'background': [] }],
            ['link', 'image', 'video'],
            ['clean'] // remove formatting
        ],
    };

    return (
        <div className="inner-pages">
        <div className="page-header">
           <h1 className="heading-primary" style={{textAlign: 'center'}}>Edit a Post</h1>
        </div>

        <div className="page-content">
                <div className="form-box-sm">
                <div className='editor'>
                    <div>
                        <FormControl fullWidth style={{margin: '15px 0'}}>
                            <TextField value={title} onChange={(e) => setTitle(e.target.value)} label="Post Title" variant="outlined" />
                        </FormControl>
                        <FormControl fullWidth style={{margin: '15px 0'}}>
                            <TextField value={short_des} onChange={(e) => setShortDes(e.target.value)} label="Post Short Description" variant="outlined" />
                        </FormControl>
                        <FormControl fullWidth style={{margin: '15px 0'}}>
                                                    <TextField value={slug} onChange={(e) => setSlug(e.target.value)} label="Post slug" variant="outlined" />
                                                </FormControl>
                                                <FormControl fullWidth style={{margin: '15px 0'}}>
                                                                            <TextField value={altText} onChange={(e) => setAltText(e.target.value)} label="Image Alt Text" variant="outlined" />
                                                                        </FormControl>
                        
                    </div>
                    <p className='nav-link-text' style={{margin: '25px 0 10px 0'}}>Post Content</p>
                   <ReactQuill 
                        style={{height:'200px'}} 
                        theme="snow" 
                        value={content} 
                        onChange={setContent} 
                        modules={modules} // Set toolbar modules here
                    />
                </div>
                    <div className="card" style={{maxWidth: '370px'}}>
                        <div>
                            <div className="image-uploader">
                                <img src={image || post.image} alt="Decoration preview" />
                                <input type="file" onChange={(e) => handleFile(e)} />
                            </div>
                            <div style={{marginTop: '20px'}}>
                               <button onClick={handleUpdate} className='btn btn-full btn-gradient'>Update Now</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    </div>
    )
}

export default EditPost;