import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import TextField from '@mui/material/TextField';

import { loginAdmin } from '../http';

import { useState } from 'react';
import { toast } from 'react-toastify';

import { useContext } from 'react';
import { AuthDispatchContext } from '../context/AuthContext';
import { NavLink } from "react-router-dom";

const Login = () => {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const dispatch = useContext(AuthDispatchContext);

    const handleLogin = async () => {
       //validating email
       if(!email || email.length === 0){
        toast.error('Email is required', {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
        })
        return;
    }

    //validating password
    if(!password || password.length === 0){
        toast.error('Password is required', {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
        })
        return;
    }

    //hsdeling login
    try {
        const {data} = await toast.promise(
            loginAdmin({email, password}),
            {
              pending: 'Logging in...',
              success: 'Logged in',
            }
        )
        if(data.admin){
           dispatch({type: 'login', admin: data.admin});
        }
    } catch (error) {
        toast.error(error.response.data.message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
        })
        return
    }
    }




    return (
        <div className="loginpage">
            <div className="row">
                <div className="col login-left"></div>
                <div className="col login-right">
                    <div className="login-content">
                        <img src="/admin/logoc.png" />
                        <div className="card">
                            <h1 className="sub-heading">Admin Login</h1>
                            <div className="login form">
                               <FormControl fullWidth style={{margin: '15px 0'}}>
                                  <TextField defaultValue={email} onChange={(e) => setEmail(e.target.value)} type='email' label="Email" variant="outlined" />
                               </FormControl>
                               <FormControl fullWidth style={{margin: '15px 0'}}>
                                  <TextField defaultValue={password} onChange={(e) => setPassword(e.target.value)} type='password' label="Password" variant="outlined" />
                               </FormControl>
                               <div style={{marginBottom: '15px', width: '100%', justifyContent: 'end', display: 'flex'}}>
                               <NavLink to={'/admin/forgot-password'}><button class='action-btn' style={{marginRight: '10px'}}>
                                            <span>Forgot Password</span>
                                        </button></NavLink>
                               </div>
                               <button onClick={handleLogin} className='btn btn-full btn-gradient'>Login</button>
                            </div>
                        </div>
                    </div>
                    <span style={{position: 'absolute', bottom: '30px'}}>
                    <span className="footer-link">Powered By <a href="https://www.waytowebs.com" target="_blank" className="footer-link"><img style={{width:'20px', margin: '0 2px'}} src="/admin/assets/img/waytowebs-icon.png" />Waytowebs</a></span>
                </span>
                </div>
            </div>
        </div>
    )
}

export default Login;