import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import TextField from '@mui/material/TextField';

const Update = ({password, setPassword, confirmPaddword, setConfirmPasswrod, handleUpdate}) => {
    return (
        <div className="card">
        <h1 className="sub-heading">Create a password</h1>
        <div className="login form">
           <FormControl fullWidth style={{margin: '15px 0'}}>
              <TextField defaultValue={password} onChange={(e) => setPassword(e.target.value)} type='password' label="Password" variant="outlined" />
           </FormControl>
           <FormControl fullWidth style={{margin: '15px 0'}}>
              <TextField defaultValue={confirmPaddword} onChange={(e) => setConfirmPasswrod(e.target.value)} type='password' label="Confirm Password" variant="outlined" />
           </FormControl>
           <button onClick={handleUpdate} className='btn btn-full btn-gradient'>Update</button>
        </div>
    </div>
    )
}

export default Update;