

const Splash = () => {
    return (
        <div style={{
            width: '100%',
            height: '100vh',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
        }}>
            <img
            alt="logo"
            src="/admin/logoc.png"
            style={{
                width: '150px'
            }}
            />
        </div>
    )
}

export default Splash;